<template>
  <div class="container">
    <div class="title_d_c">
      <div class="title_d_b_c">
        <!-- <button type="button" @click="save()" class="btn btn-default btn-sm btn_c"></button> -->
        <button type="button" @click="complete()" class="btn btn-primary btn-sm btn_c"></button>
        <button type="button" @click="goOnline()" class="btn btn-primary btn-sm btn_c">&</button>
      </div>
      <div class="title_d_d_c">
        <!-- <span class="title_s_c"></span> -->
        <input v-model="articleTitleName" class="title_i_c" placeholder=". . .">
      </div>
      <div class="title_d_d_c">
        <!-- <span class="title_s_c"></span> -->
        <textarea
          v-model="articleIntroduction"
          placeholder=". . ."
          rows="3"
          class="title_i_2_c"
          cols="20"
        ></textarea>
      </div>
      <hr>
    </div>

    <mavon-editor
      v-model="editorValue"
      ref="md"
      @imgAdd="imgAdd"
      :toolbars="markdownOption"
      class="mavon_c"
    />
    <div v-wechat-title="title"></div>
  </div>
</template>

<script >
import VueMarkdown from "vue-markdown";
var qs = require("qs");
import { formatDate } from "@/common/date.js"; //date.js
//
import * as qiniu from "qiniu-js";

export default {
  name: "Add",
  filters: {
    formatDate(time) {
      var date = new Date(time);
      return formatDate(date, "yyyy-MM-dd hh:mm"); //    'yyyy : MM : dd'   2018125
    },
    formatDateTwo(time) {
      var date = new Date(time);
      return formatDate(date, "hh:mm:ss"); //  211212
    }
  },
  data() {
    return {
      title: "",
      articleTitleName: "",
      articleIntroduction: "",
      editorValue: "",
      articleId: "",
      markdownOption: {
        bold: true, // 
        italic: true, // 
        header: true, // 
        underline: true, // 
        strikethrough: true, // 
        mark: true, // 
        superscript: true, // 
        subscript: true, // 
        quote: true, // 
        ol: true, // 
        ul: true, // 
        link: true, // 
        imagelink: true, // 
        code: true, // code
        table: true, // 
        fullscreen: true, // 
        readmodel: true, // 
        htmlcode: true, // html
        help: true, // 
        /* 1.3.5 */
        undo: true, // 
        redo: true, // 
        trash: true, // 
        save: false, // eventssave
        /* 1.4.2 */
        navigation: true, // 
        /* 2.1.8 */
        alignleft: true, // 
        aligncenter: true, // 
        alignright: true, // 
        /* 2.2.1 */
        subfield: true, // 
        preview: true // 
      }
    };
  },
  components: {
    VueMarkdown
  },
  methods: {
    complete() {
      if (this.check() == null) {
        return;
      }
      this.$post("/manage/add/complete", {
        articleId: this.articleId,
        articleTitleName: this.articleTitleName,
        articleIntroduction: this.articleIntroduction,
        articleState: "0",
        backupFieldOne: this.editorValue
      }).then(response => {
        // console.log(response);
        if (response.code == "10200") {
          this.$notify({
            title: "",
            position: "top-left",
            type: "success"
          });
          this.$router.push({ name: "article" });
        } else {
          this.$notify({
            title: "",
            position: "top-left",
            type: "error"
          });
        }
      });
    },
    goOnline() {
      if (this.check() == null) {
        return;
      }
      this.$post("/manage/add/complete", {
        articleId: this.articleId,
        articleTitleName: this.articleTitleName,
        articleIntroduction: this.articleIntroduction,
        articleState: "1",
        backupFieldOne: this.editorValue
      }).then(response => {
        // console.log(response);
        if (response.code == "10200") {
          this.$router.push({ name: "article" });
          this.$notify({
            title: "",
            position: "top-left",
            type: "success"
          });
        } else {
          this.$notify({
            title: "",
            position: "top-left",
            type: "error"
          });
        }
      });
    },
    check() {
      if (!this.articleTitleName) {
        this.$message({
          message: "",
          center: true,
          type: "error",
          showClose: true
        });
        return null;
      } else if (!this.articleIntroduction) {
        this.$message({
          message: "",
          center: true,
          type: "error",
          showClose: true
        });
        return null;
      } else if (!this.editorValue) {
        this.$message({
          message: "",
          center: true,
          type: "error",
          showClose: true
        });
        return null;
      }
      return 1;
    },
    uploadImage(image, uptoken, pos) {
      var perfix = "assets/";
      var suffix = image.name.substring(image.name.lastIndexOf("."));
      let config = {
        //useCdnDomain: true,
        region: qiniu.region.z0 //
      };

      var putExtra = {
        fname: "",
        params: {},
        mimeType: [] || null
      };
      var observable = qiniu.upload(
        image,
        perfix + uptoken.key + suffix,
        uptoken.token,
        putExtra,
        config
      );
      var thisObj = this;
      observable.subscribe({
        next: result => {
          // 
          console.log(result);
        },
        error: errResult => {
          // 
          alert(errResult);
          console.log(errResult);
        },
        complete: result => {
          // 
          // console.log(result);
          var url =
            "http://image.lujunwei.com/" + perfix + uptoken.key + suffix;
          thisObj.$refs.md.$img2Url(pos, url);
        }
      });
    },
    imgAdd(pos, image) {
      var suffix = image.name.substring(image.name.lastIndexOf("."));
      if (suffix != ".jpg" && suffix != ".png") {
        alert("");
        return;
      }
      var thisObj = this;
      thisObj.$post("/manage/add/image/token", {}).then(response => {
        if (response.code == "10200") {
          thisObj.uploadImage(image, response.result, pos);
        } else {
          alert("token");
        }
      });
    }
  },
  beforeMount: function() {
    this.$post("/manage/add/articleiNextId", {}).then(response => {
      // console.log(response);
      if (response.code == "10200") {
        this.articleId = response.result;
        console.log(response);
      } else {
        alert("ID");
      }
    });
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.btn_c {
  margin-left: 0%;
}
.title_d_c {
  margin-top: 2%;
}
.title_s_c {
  font-size: 20px;
  font-weight: bold;
}
.title_d_d_c {
  margin-top: 1%;
}
.title_i_c {
  padding: 0.5%;
  overflow: visible;
  width: 100%;
  font-size: 20px;
  padding-left: 1.5%;
  padding-right: 1.5%;
}
.title_i_2_c {
  padding: 0.5%;
  overflow: visible;
  width: 100%;
  font-size: 16px;
  font-style: italic;
  padding-left: 1.5%;
  padding-right: 1.5%;
}
.mavon_c {
  margin-bottom: 5%;
}
</style>


